@import url(https://fonts.googleapis.com/css?family=Montserrat);
@import url(https://fonts.googleapis.com/css?family=Raleway|ZCOOL+XiaoWei);
.container {
    position: relative;
    display: grid;
    overflow: hidden;
}

* {
    margin: 0;
    padding: 0;
}


#slide img {
    margin: 0;
    padding: 0;
    height: 100%;
    width: 100vw;
    overflow-x: hidden;
    height: auto;
    size: cover;
    border: 1px solid white;
    border-radius: 2%; 
}

#slide {
    margin: 0;
    padding: 0;
    display: grid;
    grid-template-rows: auto;
    align-content: center;
    position: relative;
     z-index: 0;
    font-family: 'Montserrat',
    sans-serif;
    overflow-x: hidden;
}



#header {
    position: relative;
    margin: 0;
    padding: 0;
}


.App {
  text-align: center;
}

