* {
  margin: 0;
  padding: 0;
}

body, html {
  height: 100vh;
  width: 100vw;
  margin: 0;
  padding: 0;
  /* overflow: hidden; */
  cursor: pointer;
}

#thumb {
  margin: 0;
  padding: 0;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-self: center;
  z-index: 1;
} 

#Unlike {
  position: absolute;
  top: 75%;
  left: 1%;
}

#Like {
  position: absolute;
  top: 75%;
  left: 80%;
}

#filter svg {
  left: 2px;
  position: absolute;
  top: 12%;
  fill: black;
}

#basket svg {
  position: absolute;
  top: 12%;
  right: 0px;
}

#header header {
  margin-top: 20px; 
  color: white;
}


#second svg {
  position: absolute;
  top: 12%;
  width: 20px;
  height: 20px;
  left: 48%;
}

.info {
  position: absolute;
  top: -50%;
  z-index: 1;
}

h2 {
  font-size: 1rem;
  font-family: 'ZCOOL XiaoWei', serif;
  margin: 0;
  padding: 0;
}

h3 {
  font-size: 0.8rem;
  font-family: 'ZCOOL XiaoWei', serif;
  margin: 1rem;
  padding: 0;
}
