#secondview {
    position: relative;
    display: grid;
    grid-template-rows: 3fr auto;
    grid-row-gap: 0px;
    margin: 0;
    padding: 0;
}

.split {
    margin: 0;
    padding: 0;
    /* display: grid;
    grid-template-columns: 1fr 1fr; */
    display: flex;
    flex-direction: row;
}

#secondnav {
    z-index: 1;
}

